'use client';

import {
    Dialog,
    Box,
    Button,
    Card,
    CardContent,
    TextField,
    Typography,
    Grid,
    MenuItem,
    Modal,
    IconButton,
} from '@mui/material';
import { useMediaQuery } from '@mui/material';
import { useTheme } from '@emotion/react';
import {
    IconCheveronLeft,
    IconTrash,
    IconInformationCircle,
    IconX,
    IconPencil,
} from '@intersect.mbo/intersectmbo.org-icons-set';
import { formatIsoDate } from '../../lib/utils';
import { useEffect, useState } from 'react';
import { LinkManager } from '../CreationGoveranceAction';
import { deleteProposal, createProposalContent } from '../../lib/api';
import { useAppContext } from '../../context/context';
import { useNavigate } from 'react-router-dom';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: {
        xs: '90%',
        sm: '50%',
        md: '30%',
    },
    bgcolor: 'background.paper',
    boxShadow: 24,
    borderRadius: '20px',
};

const EditProposalDialog = ({
    proposal,
    openEditDialog,
    handleCloseEditDialog,
    handleClose,
    governanceActionTypes,
    setMounted,
    maxLength = 256,
}) => {
    const navigate = useNavigate();
    const { setLoading } = useAppContext();
    const theme = useTheme();
    const [draft, setDraft] = useState({});
    const [isSaveDisabled, setIsSaveDisabled] = useState(true);
    const [openSaveDraftModal, setOpenSaveDraftModal] = useState(false);
    const [openPublishftModal, setOpenPublishModal] = useState(false);

    const isSmallScreen = useMediaQuery((theme) =>
        theme.breakpoints.down('sm')
    );

    const handleIsSaveDisabled = () => {
        if (
            draft?.gov_action_type_id &&
            draft?.prop_name &&
            draft?.prop_abstract &&
            draft?.prop_motivation &&
            draft?.prop_rationale &&
            draft?.prop_receiving_address &&
            draft?.prop_amount
        ) {
            setIsSaveDisabled(false);
        } else {
            setIsSaveDisabled(true);
        }
    };

    const setDraftData = (proposalData) => {
        const draft = {
            proposal_id: proposalData?.id,
            gov_action_type_id:
                proposalData?.attributes?.content?.attributes
                    ?.gov_action_type_id,
            prop_abstract:
                proposalData?.attributes?.content?.attributes?.prop_abstract,
            prop_amount:
                proposalData?.attributes?.content?.attributes?.prop_amount,
            prop_motivation:
                proposalData?.attributes?.content?.attributes?.prop_motivation,
            prop_name: proposalData?.attributes?.content?.attributes?.prop_name,
            prop_rationale:
                proposalData?.attributes?.content?.attributes?.prop_rationale,
            prop_receiving_address:
                proposalData?.attributes?.content?.attributes
                    ?.prop_receiving_address,
            proposal_links:
                proposalData?.attributes?.content?.attributes?.proposal_links,
        };

        return draft;
    };

    const handleDeleteProposal = async () => {
        setLoading(true);
        try {
            const response = await deleteProposal(proposal?.id);
            if (!response) return;

            navigate(`/proposal_discussion`);
        } catch (error) {
            console.error('Failed to delete proposal:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleUpdatePorposal = async (isDraft = false) => {
        setLoading(true);

        let proposalConentObj = {};

        if (isDraft === true) {
            proposalConentObj.is_draft = true;
            proposalConentObj.prop_rev_active = true;
        } else {
            proposalConentObj.prop_rev_active = true;
        }

        try {
            const response = await createProposalContent({
                ...draft,
                ...proposalConentObj,
            });
            if (!response) return;
        } catch (error) {
            console.error('Failed to delete proposal:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleOpenSaveDraftModal = () => {
        setOpenSaveDraftModal(true);
    };

    const handleCloseSaveDraftModal = () => {
        setOpenSaveDraftModal(false);
    };

    const handleOpenPublishModal = () => {
        setOpenPublishModal(true);
    };

    const handleClosePublishModal = () => {
        setOpenPublishModal(false);
    };

    useEffect(() => {
        setDraft(setDraftData(proposal));
    }, [proposal]);

    useEffect(() => {
        handleIsSaveDisabled();
    }, [draft]);

    return (
        <Box>
            <Dialog
                fullScreen
                open={openEditDialog}
                onClose={handleCloseEditDialog}
            >
                <Grid
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        flexGrow: 1,
                        backgroundImage: `url('/svg/ellipse-1.svg'), url('/svg/ellipse-2.svg')`,
                        backgroundRepeat: 'no-repeat, no-repeat',
                        backgroundPosition: 'top left, bottom right',
                        backgroundSize: 'auto, auto',
                        overflow: 'auto',
                        minHeight: 0,
                    }}
                >
                    <Grid
                        item
                        sx={{
                            borderBottom: `1px solid ${theme.palette.border.gray}`,
                            pl: 4,
                            mt: 2,
                        }}
                    >
                        <Typography variant='h4' component='h1' gutterBottom>
                            Edit Proposal
                        </Typography>
                    </Grid>
                    <Grid item m={2}>
                        <Button
                            size='small'
                            startIcon={
                                <IconCheveronLeft
                                    width='18'
                                    height='18'
                                    fill={theme.palette.primary.main}
                                />
                            }
                            onClick={() => {
                                handleCloseEditDialog();
                                handleClose();
                            }}
                        >
                            Back
                        </Button>
                    </Grid>
                    <Grid
                        xs={12}
                        item
                        display='flex'
                        justifyContent='center'
                        alignContent='center'
                        m={2}
                    >
                        <Grid
                            xs={11}
                            md={5}
                            item
                            zIndex={1}
                            maxWidth='910px'
                            width='100%'
                        >
                            <Card variant='outlined'>
                                <CardContent
                                    sx={{
                                        ml: {
                                            xs: 0,
                                            sm: 5,
                                            md: 5,
                                            lg: 15,
                                        },
                                        mr: {
                                            xs: 0,
                                            sm: 5,
                                            md: 5,
                                            lg: 15,
                                        },
                                    }}
                                >
                                    <Box
                                        sx={{
                                            display: 'flex',
                                            flexDirection: 'column',
                                            gap: 2,
                                        }}
                                    >
                                        <Box
                                            sx={{
                                                align: 'center',
                                                textAlign: 'center',
                                                mt: 2,
                                            }}
                                        >
                                            <Typography
                                                variant='subtitle2'
                                                color={(theme) =>
                                                    theme.palette.text.orange
                                                }
                                                gutterBottom
                                            >
                                                REQUIRED
                                            </Typography>

                                            <Typography
                                                variant='h4'
                                                gutterBottom
                                            >
                                                Proposal Details
                                            </Typography>
                                            <Typography
                                                variant='subtitle'
                                                color={(theme) =>
                                                    theme.palette.text.grey
                                                }
                                                gutterBottom
                                            >
                                                Subtext to describe something if
                                                needed
                                            </Typography>

                                            <Box>
                                                <Button
                                                    variant='outlined'
                                                    sx={{
                                                        mt: 2,
                                                        mb: 2,
                                                        color: 'black',
                                                        borderColor: (theme) =>
                                                            theme.palette
                                                                .primary
                                                                .lightGray,
                                                        '&:hover': {
                                                            backgroundColor:
                                                                'rgba(0, 0, 0, 0.08)',
                                                            borderColor:
                                                                'black',
                                                            color: 'black',
                                                        },
                                                    }}
                                                    startIcon={
                                                        <IconTrash
                                                            width='18'
                                                            height='18'
                                                        />
                                                    }
                                                    onClick={
                                                        handleDeleteProposal
                                                    }
                                                >
                                                    Delete Proposal
                                                </Button>
                                            </Box>
                                        </Box>

                                        <Box
                                            sx={{
                                                display: 'flex',
                                                flexDirection: 'column',
                                                textAlign: 'center',
                                                mb: 2,
                                            }}
                                        >
                                            <Box>
                                                <Typography
                                                    variant='body2'
                                                    component='p'
                                                    sx={{
                                                        display: 'flex',
                                                        justifyContent:
                                                            'center',
                                                        alignItems: 'center',
                                                        borderTopLeftRadius:
                                                            '16px',
                                                        borderTopRightRadius:
                                                            '16px',
                                                        borderBottomLeftRadius: 0,
                                                        borderBottomRightRadius: 0,
                                                        gap: 1,
                                                        backgroundColor:
                                                            '#B8CDFF',
                                                        color: 'text.black',
                                                    }}
                                                >
                                                    <IconInformationCircle />
                                                    {`Submit: ${formatIsoDate(
                                                        proposal?.attributes
                                                            ?.createdAt
                                                    )}`}
                                                </Typography>
                                            </Box>
                                            <Box>
                                                <Typography
                                                    variant='body2'
                                                    component='p'
                                                    sx={{
                                                        display: 'flex',
                                                        justifyContent:
                                                            'center',
                                                        alignItems: 'center',
                                                        borderTopLeftRadius: 0,
                                                        borderTopRightRadius: 0,
                                                        borderBottomLeftRadius:
                                                            '16px',
                                                        borderBottomRightRadius:
                                                            '16px',
                                                        gap: 1,
                                                        backgroundColor:
                                                            '#F3F4F8',
                                                        color: 'text.black',
                                                    }}
                                                >
                                                    <IconInformationCircle />
                                                    {`Last Edit: ${
                                                        proposal?.attributes
                                                            ?.updatedAt
                                                            ? formatIsoDate(
                                                                  proposal
                                                                      ?.attributes
                                                                      ?.updatedAt
                                                              )
                                                            : '--'
                                                    }`}
                                                </Typography>
                                            </Box>
                                        </Box>

                                        <TextField
                                            select
                                            label='Governance Action Type'
                                            fullWidth
                                            required
                                            value={
                                                draft?.gov_action_type_id || ''
                                            }
                                            onChange={(e) => {
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    gov_action_type_id:
                                                        e.target.value,
                                                }));
                                            }}
                                        >
                                            {governanceActionTypes?.map(
                                                (option) => (
                                                    <MenuItem
                                                        key={option?.value}
                                                        value={option?.value}
                                                    >
                                                        {option?.label}
                                                    </MenuItem>
                                                )
                                            )}
                                        </TextField>

                                        <TextField
                                            fullWidth
                                            label='Title'
                                            variant='outlined'
                                            value={draft?.prop_name || ''}
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_name: e.target.value,
                                                }))
                                            }
                                            required
                                        />

                                        <TextField
                                            size='large'
                                            name='Abstract'
                                            label='Abstract'
                                            placeholder='Summary...'
                                            multiline
                                            rows={isSmallScreen ? 10 : 4}
                                            value={draft?.prop_abstract || ''}
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_abstract:
                                                        e.target.value,
                                                }))
                                            }
                                            required
                                            helperText={
                                                <>
                                                    <Typography variant='caption'>
                                                        * General Summary of
                                                        your proposal
                                                    </Typography>
                                                    <Typography
                                                        variant='caption'
                                                        sx={{ float: 'right' }}
                                                    >
                                                        {`${
                                                            draft?.prop_abstract
                                                                ?.length || 0
                                                        }/${maxLength}`}
                                                    </Typography>
                                                </>
                                            }
                                            InputProps={{
                                                inputProps: {
                                                    maxLength: maxLength,
                                                },
                                            }}
                                        />

                                        <TextField
                                            size='large'
                                            name='Motivation'
                                            label='Motivation'
                                            placeholder='Problem this will solve'
                                            multiline
                                            rows={isSmallScreen ? 10 : 4}
                                            value={draft?.prop_motivation || ''}
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_motivation:
                                                        e.target.value,
                                                }))
                                            }
                                            required
                                            helperText={
                                                <>
                                                    <Typography variant='caption'>
                                                        * How will this solve a
                                                        problem
                                                    </Typography>
                                                    <Typography
                                                        variant='caption'
                                                        sx={{ float: 'right' }}
                                                    >
                                                        {`${
                                                            draft
                                                                ?.prop_motivation
                                                                ?.length || 0
                                                        }/${maxLength}`}
                                                    </Typography>
                                                </>
                                            }
                                            InputProps={{
                                                inputProps: {
                                                    maxLength: maxLength,
                                                },
                                            }}
                                        />

                                        <TextField
                                            size='large'
                                            name='Rationale'
                                            label='Rationale'
                                            placeholder='Problem this will solve'
                                            multiline
                                            rows={isSmallScreen ? 10 : 4}
                                            value={draft?.prop_rationale || ''}
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_rationale:
                                                        e.target.value,
                                                }))
                                            }
                                            required
                                            helperText={
                                                <>
                                                    <Typography variant='caption'>
                                                        * Put all the content of
                                                        the Proposal here
                                                    </Typography>
                                                    <Typography
                                                        variant='caption'
                                                        sx={{ float: 'right' }}
                                                    >
                                                        {`${
                                                            draft
                                                                ?.prop_rationale
                                                                ?.length || 0
                                                        }/${maxLength}`}
                                                    </Typography>
                                                </>
                                            }
                                            InputProps={{
                                                inputProps: {
                                                    maxLength: maxLength,
                                                },
                                            }}
                                        />

                                        <TextField
                                            fullWidth
                                            margin='normal'
                                            label='Receiving address'
                                            variant='outlined'
                                            value={
                                                draft?.prop_receiving_address ||
                                                ''
                                            }
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_receiving_address:
                                                        e.target.value,
                                                }))
                                            }
                                            required
                                        />

                                        <TextField
                                            fullWidth
                                            margin='normal'
                                            label='Amount'
                                            type='number'
                                            variant='outlined'
                                            placeholder='e.g. 2000'
                                            value={draft?.prop_amount || ''}
                                            onChange={(e) =>
                                                setDraft((prev) => ({
                                                    ...prev,
                                                    prop_amount: e.target.value,
                                                }))
                                            }
                                            required
                                        />

                                        <Box
                                            sx={{
                                                display: 'flex',
                                                flexDirection: 'column',
                                                justifyContent: 'center',
                                                alignItems: 'center',
                                                mt: 2,
                                            }}
                                        >
                                            <Typography
                                                variant='subtitle2'
                                                color={(theme) =>
                                                    theme.palette.text.orange
                                                }
                                                gutterBottom
                                            >
                                                OPTIONAL
                                            </Typography>

                                            <Typography
                                                variant='h5'
                                                textAlign='center'
                                                gutterBottom
                                            >
                                                References and Supporting
                                                Information
                                            </Typography>

                                            <Typography
                                                variant='subtitle2'
                                                textAlign='center'
                                                color={(theme) =>
                                                    theme.palette.text.grey
                                                }
                                                gutterBottom
                                            >
                                                Links to extra content or social
                                                media contacts (maiximum of 7
                                                entries)
                                            </Typography>
                                        </Box>
                                        <LinkManager
                                            proposalData={draft}
                                            setProposalData={setDraft}
                                        />
                                    </Box>
                                    <Box
                                        sx={{
                                            display: 'flex',
                                            flexDirection: isSmallScreen
                                                ? 'column'
                                                : 'row',
                                            justifyContent: 'space-between',
                                            mt: 10,
                                        }}
                                    >
                                        <Box>
                                            <Button
                                                variant='outlined'
                                                startIcon={
                                                    <IconPencil
                                                        fill={
                                                            theme.palette
                                                                .primary.main
                                                        }
                                                    />
                                                }
                                                sx={{
                                                    mb: { xs: 2, md: 0 },
                                                }}
                                                fullWidth={isSmallScreen}
                                                onClick={() => {
                                                    handleCloseEditDialog();
                                                    handleClose();
                                                }}
                                            >
                                                Back
                                            </Button>
                                        </Box>
                                        <Box
                                            sx={{
                                                display: 'flex',
                                                justifyContent: 'space-between',
                                                gap: 2,
                                                width: isSmallScreen
                                                    ? '100%'
                                                    : 'auto',
                                            }}
                                        >
                                            <Box
                                                sx={{
                                                    width: '100%',
                                                    mr: 2,
                                                }}
                                            >
                                                <Button
                                                    variant='text'
                                                    fullWidth
                                                    disabled={isSaveDisabled}
                                                    onClick={async () => {
                                                        await handleUpdatePorposal(
                                                            true
                                                        );
                                                        handleOpenSaveDraftModal();
                                                        setMounted(false);
                                                    }}
                                                >
                                                    Save Draft
                                                </Button>
                                            </Box>
                                            <Box
                                                sx={{
                                                    flexGrow: 1,
                                                }}
                                            >
                                                <Button
                                                    variant='contained'
                                                    sx={{
                                                        whiteSpace: 'nowrap',
                                                    }}
                                                    fullWidth
                                                    disabled={isSaveDisabled}
                                                    onClick={() => {
                                                        handleOpenPublishModal();
                                                    }}
                                                >
                                                    Publish with new edits
                                                </Button>
                                            </Box>
                                        </Box>
                                    </Box>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                </Grid>
                <Modal
                    open={openSaveDraftModal}
                    onClose={handleCloseSaveDraftModal}
                >
                    <Box sx={style}>
                        <Box
                            pt={2}
                            pl={2}
                            pr={2}
                            pb={1}
                            borderBottom={1}
                            borderColor={(theme) =>
                                theme.palette.border.lightGray
                            }
                        >
                            <Box
                                display='flex'
                                flexDirection='row'
                                justifyContent='space-between'
                                alignItems={'center'}
                            >
                                <Typography
                                    id='modal-modal-title'
                                    variant='h6'
                                    component='h2'
                                >
                                    Proposal saved to drafts
                                </Typography>
                                <IconButton
                                    onClick={() => {
                                        handleCloseSaveDraftModal();
                                        handleCloseEditDialog();
                                        handleClose();
                                        setMounted(false);
                                    }}
                                >
                                    <IconX width='24px' height='24px' />
                                </IconButton>
                            </Box>
                        </Box>
                        <Box
                            sx={{
                                p: 2,
                                display: 'flex',
                                justifyContent: 'center',
                                alignContent: 'center',
                            }}
                        >
                            <Button
                                variant='contained'
                                fullWidth
                                onClick={() => {
                                    handleCloseSaveDraftModal();
                                    handleCloseEditDialog();
                                    handleClose();
                                }}
                            >
                                Close
                            </Button>
                        </Box>
                    </Box>
                </Modal>
                <Modal
                    open={openPublishftModal}
                    onClose={handleClosePublishModal}
                >
                    <Box sx={style}>
                        <Box
                            pt={2}
                            pl={2}
                            pr={2}
                            pb={1}
                            borderBottom={1}
                            borderColor={(theme) =>
                                theme.palette.border.lightGray
                            }
                        >
                            <Box
                                display='flex'
                                flexDirection='row'
                                justifyContent='space-between'
                                alignItems={'center'}
                            >
                                <Typography
                                    id='modal-modal-title'
                                    variant='h6'
                                    component='h2'
                                >
                                    Please confirm applied changes
                                </Typography>
                                <IconButton
                                    onClick={() => {
                                        handleClosePublishModal();
                                    }}
                                >
                                    <IconX width='24px' height='24px' />
                                </IconButton>
                            </Box>
                        </Box>
                        <Box
                            sx={{
                                p: 2,
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center',
                                alignContent: 'center',
                                gap: 2,
                            }}
                        >
                            <Button
                                variant='contained'
                                fullWidth
                                onClick={async () => {
                                    await handleUpdatePorposal(false);
                                    handleCloseSaveDraftModal();
                                    handleCloseEditDialog();
                                    handleClose();
                                    setMounted(false);
                                }}
                            >
                                Confirm
                            </Button>
                            <Button
                                variant='outlined'
                                fullWidth
                                onClick={() => {
                                    handleClosePublishModal();
                                }}
                            >
                                Cancel
                            </Button>
                        </Box>
                    </Box>
                </Modal>
            </Dialog>
        </Box>
    );
};

export default EditProposalDialog;
